// Light theme colors
$background_color: hsl(0, 0%, 97%);
$content_color: hsl(0, 0%, 100%);
$text_color: hsl(0, 0%, 0%);
$link: hsl(240, 100%, 47%);
$visited_link: $link;
$accent_1: hsl(0, 97%, 35%);
$accent_2: hsl(50, 100%, 50%);

// Dark theme colors
$dark_background_color: hsl(0, 0%, 10%);
$dark_content_color: hsl(0, 0%, 20%);
$dark_text_color: hsl(0, 0%, 100%);
$dark_link: hsl(54, 100%, 50%);
$dark_visited_link: $dark_link;
$dark_accent_1: hsl(39, 100%, 48%);
$dark_accent_2: hsl(300, 100%, 32%);