html {
    font-size: $font_size;
}

body {
    background-color: $background_color;
    color: $text_color;
    font-family: $sans_fonts, $emoji_fonts;
    line-height: 1.5;
    padding: 0 1rem;
}

a {
    color: $link;

    &:visited {
        color: $visited_link;
    }
}

h1, h2 {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 2rem;
}

sup {
    line-height: 0;
    position: relative;
}

.boxed {
    background-color: $content_color;
    border: 1px solid adjust-color($color: $background_color, $lightness: -32%);
    box-shadow: 1px 1px adjust-color($color: $background_color, $lightness: -10%);
    display: inline-block;
    padding: 1rem 2rem;
    z-index: 1;
    position: relative;
    border-radius: 2px;

    &.dark {
        background-color: adjust-color($color: $content_color, $lightness: -2%);
        z-index: 0;
    }
}

.citation-needed {
    font-family: sans-serif;
    font-size: .8rem;
    vertical-align: super;
    white-space: nowrap;
    line-height: 1;
    
    &::before {
        content: "[";
        font-style: normal;
    }
    
    &::after {
        content: "]";
        font-style: normal;
    }
    
    .citation-link {
        font-style: italic;
        color: $link;

        &:hover {
            text-decoration: underline;
        }
    }
}