.consigli_scrittura {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    .consiglio {
        display: block;
        width: 8rem;
        height: 6rem;
        font-weight: 400;
        border: 1px solid #fff;
        background-color: #000;
        font-family: $serif_fonts;
        font-size: 1.4rem;
        box-shadow: 5px 5px 0 #ccc;
        margin: 1rem;
        position: relative;
        color: invert($color: $text_color);
        border-radius: 2px;
        padding: 0.2rem 1rem;
        transition: all .1s;
        
        .title {
            padding: .2rem .4rem;
            position: absolute;
            bottom: .4rem;
            right: 0;
            background-color: rgba(0,0,0,0.4);
            text-align: right;
            width: 147px;
        }

        &:hover {
            box-shadow:5px 5px 0 rgba(0,0,0,0.6);
        }
            
        &:active {
            position: relative;
            top: 5px;
            left: 5px;
            box-shadow: none;
        }
    }
}