@media (prefers-color-scheme: dark) {
    #header {
        #logo {
            svg {
                #nome {
                    fill: $dark_text_color ;
                }
            }

            &.home svg #riga {
                fill: $dark_accent_1;
            }

            &:hover svg #background-splash {
                fill: $dark_accent_2;
            }
        }
    }
}