article {
    margin: 2rem auto;
    max-width: $max_width;
    display: block !important;

    h1 {
        font-size: 1.4rem;
        text-align: center;
        margin: 3rem 5rem 4rem 5rem;
        font-family: $serif_fonts;
        font-weight: 500;
        letter-spacing: 1px;
        border-bottom: 1px solid;
        padding-bottom: .4rem;
    }

    figure {
        max-width: 20rem;
        margin: 2rem auto;
        text-align: center;
        border-radius: .2rem;
        padding: 1rem;

        img {
            width: 100%;
            border-radius: 2px;
        }

        figcaption h4 {
            font-weight: 400;
            font-size: .8rem;
            font-family: $mono_fonts;
            margin: 0.7rem 0 0 0;
        }
    }

    footer {
        font-size: 0.8rem;
        margin-top: 4rem;
        color: adjust-color($color: $text_color, $lightness: 60%);
    }

    &.post {

        &.liste h2 {
            text-align: center;

            &::before, &::after {
                content: " ·· "
            }
        }

        &.micro header .byline {
            display: none;
        }

        .footnotes {
            font-size: .8rem;
        }

        #post-nav {
            margin-top: 1rem;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;

            .date {
                font-size: .6rem;

                &::before {
                    content: "(";
                }

                &::after {
                    content: ")";
                }
            }

            .next {
                justify-content: end;
            }

            .previous {
                justify-self: start;
            }

            p {
                margin: 0;
            }
        }
    }

    &.page {

        h2 {
            font-size: 1rem;
            margin-left: -1.8rem;
            margin-top: 2rem;
    
            &::before {
                background-image: url(/img/asterisco.svg);
                display: inline-block;
                width: .7rem;
                height: .8rem;
                content: "";
                background-position: center;
                background-repeat: no-repeat;
                padding-right: 1rem;
            }
        }
    }

    footer {
        div { display: inline; }
        .source, .tags {
            display: none;
        }
    } 
}

#go-back {
    font-size: .8rem;
    text-align: center;

    &::before {
        content: "←";
    }
}

@media only screen and (max-width: 720px) {
    article {
        margin-left: 0;
        margin-right: 0;

        h1 {
            margin-left: 1rem;
            margin-right: 1rem;
        }
    }
}
