#footer {
    margin: 4rem auto;
    font-size: .7rem;
    clear: both;
    border-top: 1px solid adjust-color($color: $background_color, $lightness: -20%);
    max-width: 22rem;
    text-align: center;
    color: adjust-color($color: $background_color, $lightness: -20%);
    
    #impressum {
        margin-top: 1rem;
    }

    p {
        margin: 0;
    }
}

@media only screen and (max-width: 720px) {
    #footer {
        margin-left: 0;
        margin-right: 0;
    }
}
